.App {
  text-align: center;
}

.App-header {
  display: flex;
  justify-content: space-between;
  margin: auto;
  width: 100%;
  max-width: 1200px;
}

.App-header .logo {
  margin: 20px;
}

.App-header .svg {
  height: 2rem;
}

.App-header .lang {
  margin: auto 20px;
}

.App-header .active {
  font-size: 1.5rem;
}

.App-Content {
  padding: 0 2rem;
}

.bg {
  width: 100%;
}

.title {
  font-size: 2rem;
  line-height: 1.4;
  font-weight: 400;
  margin: 1.5rem 0px 1rem;
}

.sub-title {
  font-size: 1.5rem;
  line-height: 1.4;
  font-weight: 400;
  padding: 0.5rem 0px 1.5rem;
  border-top: 1px solid rgb(204, 204, 204);
}

.desc {
  color: rgb(204, 204, 204);
  font-size: 1.25rem;
  line-height: 1.4;
  font-weight: 400;
  max-width: 55ch;
  margin: 0 auto 1.45rem;
}

.paper {
  display: inline-block;
  margin-top: 0.5rem;
  font-size: 1rem;
  text-align: center;
  background-color: rgb(255, 115, 36);
  color: rgb(51, 51, 51);
  text-decoration: none;
  padding: 0.5rem 0.75rem;
  border-radius: 0.25em;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  border-image: initial;
}

.App-footer {
  height: 5em;
}
